import Docxtemplater from "docxtemplater";
import PizZip from "pizzip";
import PizZipUtils from "pizzip/utils/index.js";
import { saveAs } from "file-saver";
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Button from "react-bootstrap/Button";
import { useState } from "react";
import "../src/App.css";

let districtCode = [
    ["YMT", "TST"],
    ["MK"],
    ["CSW", "SSPO"],
    ["KC", "HH"]
]

/*
12345678
22345678
32345678
42345678
52345678
62345678
72345678
*/

const perfectNameLength = 25
let districtFullName = ["Yau Tsim", "Mong Kok", "Sham Shui Po", "Kowloon City"]

function formValidation(ref_no, fax_no, tel_no) {
    let errorMsg = "The entered case RN is invalid\n";
    for (let i = 0; i < districtCode.length; i++) {
        for (let j = 0; j < districtCode[i].length; j++) {
            if (ref_no.startsWith(districtCode[i][j] + "RN")) {
                errorMsg = "";
                break;
            }
        }
    }
    let phoneRegex = /^\d{8}$/;

    if (!fax_no.match(phoneRegex))
        errorMsg += "The entered fax number is invalid\n";

    if (!tel_no.match(phoneRegex))
        errorMsg += "The entered telephone number is invalid\n";

    return errorMsg;
}

function formatizePhoneNUmber(phone_num) {
    return phone_num.slice(0, 4) + " " + phone_num.slice(4);
}

function formatizeCaseRN(rn_no) {
    return rn_no.slice(0, rn_no.indexOf("RN")) + " " + "RN" + " " + rn_no.slice(rn_no.indexOf("RN") + 2);
}

function formatizeScamList(scam_list) {
    let scams = scam_list.split("\n");
    let tableString = "";
    for (let i = 0; i < scams.length; i++) {
        let header = "(" + String.fromCharCode(97 + i) + ") ";
        let formatPhone = scams[i].replaceAll(" ", "");
        console.log(scams[i] + "-->" + formatPhone);
        let phoneLength = formatPhone.length;

        if (phoneLength == 8)
            header += "+852 ";

        tableString += header + scams[i];

        if ((i+1) % 3 == 0)
            tableString += "\n" + "      ";
        else
            tableString += "       ";
    }
    return tableString
}

function formatizeOCName(name) {
    if (name.length < perfectNameLength) {
        //Special Case : 24 Characters
        if (name.length == perfectNameLength - 1) {
            name = " " + name + " ";
        } else {
            let startSpace = Math.ceil((perfectNameLength - name.length) / 2);
            name = " ".repeat(startSpace) + name + " ".repeat(startSpace);
        }
    } else {
        name = " " + name + " ";
    }
    return name;
}

function getDistrictFullName(rn_no) {
    let code = rn_no.slice(0, rn_no.indexOf("RN"));
    for (let i = 0; i < districtCode.length; i++) {
        for (let j = 0; j < districtCode[i].length; j++) {
            if (districtCode[i][j] == code)
                return districtFullName[i];
        }
    }
}

function getTodayDate() {
    return new Date().toJSON().slice(0, 10);
}

function App() {
    const [ifr, setIfr] = useState(null);
    const [rnValue, setRnValue] = useState('');
    const [faxValue, setFaxValue] = useState('');
    const [telValue, setTelValue] = useState('');

    const rnChange = event => {
        const result = event.target.value.replace(/\D/g, '');
        setRnValue(result.slice(0, 8));
    }

    const faxChange = event => {
        const result = event.target.value.replace(/\D/g, '');
        setFaxValue(result.slice(0, 8));
    }

    const telChange = event => {
        const result = event.target.value.replace(/\D/g, '');
        setTelValue(result.slice(0, 8));
    }

    const submitHandler = (e) => {
        e.preventDefault();
        let formData = new FormData(e.target);
        let ref_no = formData.get("dis_code") + "RN" + formData.get("ref_no");
        let fax_no = formData.get("fax_no");
        let tel_no = formData.get("tel_no");
        let errorMsg = formValidation(ref_no, fax_no, tel_no);
        if (errorMsg.length == 0) {
            loadFile("./template.docx", function (error, content) {
                if (error) console.log(error);
                let pzip = new PizZip(content);
                let doc = new Docxtemplater(pzip, {
                    paragraphLoop: true,
                    linebreaks: true
                  });
                doc.setData({
                    ref_no: formatizeCaseRN(ref_no),
                    fax_no: formatizePhoneNUmber(fax_no),
                    tel_no: formatizePhoneNUmber(tel_no),
                    district_name: getDistrictFullName(ref_no),
                    date: getTodayDate(),
                    scam_list: formatizeScamList(formData.get("scam_list")),
                    oc_name: formatizeOCName(formData.get("oc_name")),
                    team_no: formData.get("team_no")
                });
                try {
                    doc.render();
                } catch (err) {
                    console.log(err);
                }
                var out = doc.getZip().generate({
                    type: "blob",
                    mimeType:
                        "application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                });
                saveAs(out, "Request for take down.docx");
            });
        } else {
            alert(errorMsg);
        }
    };
    function loadFile(url, callback) {
        PizZipUtils.getBinaryContent(url, callback);
    }
    return (
        <div className="App">
            <p/><h4>舉報可疑WhatsApp信件範本</h4><p/>
            <div className="container">
                <Form onSubmit={submitHandler}>
                    <Form.Group>
                        <InputGroup>
                            <InputGroup.Text>Case RN</InputGroup.Text>
                            <Form.Control as="select" type="select" name="dis_code" className="districtSelect" required>
                                <option value="YMT">YMT</option>
                                <option value="TST">TST</option>
                                <option value="MK">MK</option>
                                <option value="CSW">CSW</option>
                                <option value="SSPO">SSPO</option>
                                <option value="KC">KC</option>
                                <option value="HH">HH</option>
                            </Form.Control>
                            <InputGroup.Text>RN</InputGroup.Text>
                            <Form.Control type="text" name="ref_no" value={rnValue} onChange={rnChange} required />
                        </InputGroup>
                    </Form.Group>

                    <Form.Group>
                        <InputGroup>
                            <InputGroup.Text>Office Fax No.</InputGroup.Text>
                            <Form.Control type="text" name="fax_no" value={faxValue} onChange={faxChange} required />
                        </InputGroup>
                    </Form.Group>

                    <Form.Group>
                        <InputGroup>
                            <InputGroup.Text>Office Tel No.</InputGroup.Text>
                            <Form.Control type="text" name="tel_no" value={telValue} onChange={telChange} required />
                        </InputGroup>
                    </Form.Group>

                    <Form.Group>
                        <InputGroup>
                            <InputGroup.Text>Name of OC</InputGroup.Text>
                            <Form.Control type="text" name="oc_name" placeholder="CHAN Tai-man" required />
                        </InputGroup>
                    </Form.Group>

                    <Form.Group>
                        <InputGroup>
                            <InputGroup.Text>DIT Unit</InputGroup.Text>
                            <Form.Control as="select" type="select" name="team_no" required>
                                <option value="1">Team 1</option>
                                <option value="2">Team 2</option>
                                <option value="3">Team 3</option>
                                <option value="4">Team 4</option>
                                <option value="5">Team 5</option>
                                <option value="6">Team 6</option>
                                <option value="7">Team 7</option>
                                <option value="8">Team 8</option>
                                <option value="9">Team 9</option>
                                <option value="10">Team 10</option>
                            </Form.Control>
                        </InputGroup>
                    </Form.Group>

                    <Form.Group>
                        <InputGroup.Text>Suspicous Whatsapp Accounts</InputGroup.Text>
                        <Form.Control name="scam_list" as="textarea" rows="7" placeholder="每行輸入一組可疑WhatsApp號碼，本地號碼無需輸入+852，非本地號碼需輸入國際電話區號。"/>
                    </Form.Group>
                    <Button type="Submit">Generate</Button>
                </Form>
            </div>
        </div>
      );
    }
export default App;
